<script>
    import { onMount, onDestroy } from "svelte";

    import { Row, Col, Badge, Alert, Image, ListGroup, ListGroupItem, 
		Spinner, Button, InputGroup, Input, InputGroupText } from 'sveltestrap';

    import { flagIndexLoaded, helmetSelected } from '../../stores.js';
    import { GetResourceBinary, LoadIndexs } from '../../indexdb.js';

    let indexs = [];
    let resourcesHelmet = [];
    let _helmetSelected = 0;

    function selectHelmet(indexHelmet) {
      _helmetSelected = indexHelmet;
      console.log(indexHelmet);
      helmetSelected.set(indexHelmet);
    }
    
	async function showGrh(numInd, first) {

    const result = new Promise((resolve, reject) => {

      if (indexs == []) {
        reject();
      }

      if (indexs.indexs.length>0) {
        let ret = "";
        indexs.indexs.forEach(e => {
          if (e.index == numInd) {
            if (e.frames.length == 1 && e.w > 0 && e.h > 0) {
              GetResourceBinary(e.frames[0]).then(function(img) {						
                let image = "background-image: url("+ img +");";
                let position = "background-position: -" + e.x + "px -" + e.y + "px;";
                ret = `<img style="${image}${position}width:${e.w}px;height:${e.h}px;background-color: black;">`;
                resolve(ret);
              });
            } else { // Es animación!
              if(first == false) {
                ret = "";
              }
              indexs.indexs.forEach(y => {
                if (y.index == e.frames[0]) {
                  GetResourceBinary(y.frames[0]).then(function(img) {						
                    let image = "background-image: url("+ img +");";
                    let position = "background-position: -" + y.x + "px -" + y.y + "px;";
                    ret = `<img style="${image}${position}width:${y.w}px;height:${y.h}px;background-color: black;">`;
                    resolve(ret);
                  });
                };
              });
            }					
          }
        });
      };
    });

    return await result;
  }

  async function loadIndexs() {
    if (flagIndexLoaded) {
			indexs = await LoadIndexs("indexsGraphics");
      resourcesHelmet = await LoadIndexs("indexsHelmet");
		} else {
			indexs = [];
      resourcesHelmet = [];
		}
  }

  let unsubscribeGraphics = flagIndexLoaded.subscribe(_ => {
		loadIndexs();
	});

  onMount(async () => {
    loadIndexs();
  });

  onDestroy(async () => {
    unsubscribeGraphics();
  });

</script>


<Col class="input-dark">
  <ListGroup style="overflow: auto; height: 1050px;">
    {#if resourcesHelmet != undefined && resourcesHelmet != []}
      {#each resourcesHelmet as indexHelmet}
          <ListGroupItem tag="button" active={_helmetSelected == indexHelmet.index} action on:click="{selectHelmet(indexHelmet)}" >
            {indexHelmet.index}
            {#await showGrh(indexHelmet.helmet[0], true) then grh}
              {@html grh}
            {/await}
          </ListGroupItem>
      {/each}
    {/if}
  </ListGroup>
</Col>