<script>
    import { onMount, onDestroy  } from "svelte";
    
    import { indexSelected } from '../../stores.js';
    import { GetResourceBinary } from '../../indexdb.js';

    let mounted = false;

    let imageLoaded = 0;
	let imgSource = document.createElement('img');

    function drawImage(ctx, canvas) {
        let hRatio = canvas.width / imgSource.width;
        let vRatio = canvas.height / imgSource.height;
        let ratio  = Math.min ( hRatio, vRatio );
        ctx.drawImage(imgSource, 0, 0, imgSource.width, imgSource.height, 0,0,imgSource.width*ratio, imgSource.height*ratio);
        ctx.lineWidth = 2;
        ctx.strokeStyle = "lime";
        ctx.setLineDash([2,5]);
        ctx.strokeRect($indexSelected.x*ratio, $indexSelected.y*ratio, $indexSelected.w*ratio, $indexSelected.h*ratio);
        ctx.stroke();
    }

    async function loadPreview() {

        if ($indexSelected == undefined) {
            return;
        }

        if ($indexSelected.frames[0] == undefined) {
            return;
        }

        let canvas = document.getElementById("sourcepreview");
		let ctx = canvas.getContext('2d');
		ctx.imageSmoothingEnabled = false;
		ctx.imageSmoothingQuality = 'high';

		ctx.fillStyle = "black";
		ctx.fillRect(0, 0, canvas.width, canvas.height);
        ctx.clearRect(0, 0,  canvas.height, canvas.width);

        if (imageLoaded == $indexSelected.frames[0]) {
            drawImage(ctx, canvas);
			return;
		}

        imgSource.crossOrigin = "Anonymous";
		imgSource.onload = function() {
            drawImage(ctx, canvas);
		}

        GetResourceBinary($indexSelected.frames[0]).then(function(img) {	
			imgSource.src = img;
			imageLoaded = $indexSelected.frames[0];
		});
    }

    let unsubscribe = indexSelected.subscribe(_ => {
        if (mounted) {
            loadPreview();
        };
    });

    onMount(async () => {
        mounted = true;
		loadPreview();
	});

    onDestroy(async () => {
        mounted = false;
        imageLoaded = 0;
        imgSource = document.createElement('img');
        unsubscribe();
    });

</script>

<canvas id="sourcepreview" width="300px" height="300px"></canvas>

<style>
	canvas {
        border:1px solid #000000;
        background-color: black;
		border-color: black;
        display: block;
        margin: 0 auto;
	}
</style>