<script>
    import { onMount, onDestroy  } from "svelte";

    import { Row, Col, Badge, Alert, Image, ListGroup, ListGroupItem,
		Spinner, Button, InputGroup, Input, InputGroupText } from 'sveltestrap';

    import { flagIndexLoaded, indexSelected, events } from '../../stores.js';
    import { LoadIndexs } from '../../indexdb.js';

    import IndexPreviewAnimated from './indexPreviewAnimated.svelte';
    import IndexPreviewResource from './indexPreviewResource.svelte';

    let indexs = [];

    function deleteFrame(frame) {
        console.log('deleteFrame', frame);
        console.log('frames', $indexSelected.frames);

        let newFrames = [];
        for (let i = 0; i < $indexSelected.frames.length; i++) {
            if (i !== frame) {
                newFrames.push($indexSelected.frames[i]);
            }
        }

        console.log('newFrames', newFrames);
        $indexSelected.frames = newFrames;
    }

    function addFrame() {
        $indexSelected.frames = [...$indexSelected.frames, 0];
    }

    function convertIndex(type) {
        if (type==0) {
            let res = confirm("¿Estás seguro de convertir el index a tipo estatico?");
            if (res) {
                $indexSelected.type = 0;
                $indexSelected.frames = [0];
            }
        } else {
            let res = confirm("¿Estás seguro de convertir el index a tipo animado?");
            if (res) {
                $indexSelected.type = 1;
                $indexSelected.frames = [0,0];
            }
        }
    }

    function deleteIndex() {
		let index = $indexSelected.index;

        console.log('deleteIndex', index);
        console.log("indexs.length = " + indexs.length);

		let newIndexs = [];

		for (let i = 0; i < indexs.length; i++) {
            let value = indexs[i].index;
            if (value != index) {
                newIndexs.push(indexs[i]);
			}
		}

        console.log("newIndexs.length = " + newIndexs.length);

		indexs = newIndexs;
		indexSelected.set(null);
        events.set({"action": "deleteIndex", "index": index});
	}

    async function loadIndexs() {
		if (flagIndexLoaded) {
			indexs = await LoadIndexs("indexsGraphics");
		} else {
			indexs = [];
		}
	}

    let unsubscribeGraphics = flagIndexLoaded.subscribe(_ => {
		loadIndexs();
	});

    onMount(async () => {
		loadIndexs();
	});

    onDestroy(async () => {
        unsubscribeGraphics();
    });

</script>

<Col class="input-dark">
{#if $indexSelected != null &&
    $indexSelected != undefined &&
    $indexSelected.frames != undefined}
    <IndexPreviewAnimated />
    <InputGroup>
        <InputGroupText>
            {#if $indexSelected.frames.length==1}
                <Badge>Estatico</Badge>
            {:else}
                <Badge>Animación</Badge>
            {/if}
        </InputGroupText>
        <Input disabled type="number" value={$indexSelected.index} class="disabled" />
    </InputGroup>
    {#if $indexSelected.frames.length==1}
        <InputGroup><IndexPreviewResource /></InputGroup>
        <Col>
            <InputGroup>
                <InputGroupText>GRH</InputGroupText>
                <Input type="number" bind:value={$indexSelected.frames[0]} />
            </InputGroup>
        </Col>

        <Row noGutters>
            <Col>
                <InputGroup>
                    <InputGroupText>X</InputGroupText>
                    <Input type="number" bind:value={$indexSelected.x} />
                </InputGroup>
            </Col>
            <Col>
                <InputGroup>
                    <InputGroupText>Y</InputGroupText>
                    <Input type="number" bind:value={$indexSelected.y} />
                </InputGroup>
            </Col>
        </Row>
        <Row noGutters>
            <Col>
                <InputGroup>
                    <InputGroupText title="Ancho">↔️</InputGroupText>
                    <Input type="number" bind:value={$indexSelected.w} />
                </InputGroup>
            </Col>
            <Col>
                <InputGroup>
                    <InputGroupText title="Alto">↕️</InputGroupText>
                    <Input type="number" bind:value={$indexSelected.h} />
                </InputGroup>
            </Col>
        </Row>

        <Col>
            <InputGroup>
                <InputGroupText>Index</InputGroupText>
                <Input value="Grh{$indexSelected.index}=1-{$indexSelected.frames[0]}-{$indexSelected.x}-{$indexSelected.y}-{$indexSelected.w}-{$indexSelected.h}" class="disabled"  disabled/>
            </InputGroup>
        </Col>

        <hr />

        <Col>
            <InputGroup>
                <Button style="width: inherit;" color="danger" on:click={() => convertIndex(1)}>⚠️ Convertir en animación ⚠️</Button>
            </InputGroup>
        </Col>
    {:else}
        <Col>
            {#each $indexSelected.frames as _, frame}
                <InputGroup>
                    <Input type="number" bind:value={$indexSelected.frames[frame]} />
                    <Button color="danger" on:click={() => deleteFrame(frame)}>X</Button>
                </InputGroup>
            {/each}
            <InputGroup style="text-align: right; display: inline-block;" >
                <Button color="primary" on:click={() => addFrame()}>Añadir</Button>
            </InputGroup>
        </Col>
        <Col>
            <InputGroup>
                <InputGroupText title="Velocidad">⏱️</InputGroupText>
                <Input type="number" bind:value={$indexSelected.speed} />
            </InputGroup>
        </Col>

        <Col>
            <InputGroup>
                <InputGroupText>Index</InputGroupText>
                <Input value="Grh{$indexSelected.index}={$indexSelected.frames.length}-{$indexSelected.frames.join('-')}-{$indexSelected.speed}" class="disabled"  disabled/>
            </InputGroup>
        </Col>

        <hr />

        <Col>
            <InputGroup>
                <Button style="width: inherit;" color="danger" on:click={() => convertIndex(0)}>⚠️ Convertir en estatico ⚠️ </Button>
            </InputGroup>
        </Col>
    {/if}

    <Col>
        <InputGroup>
            <Button style="width: inherit;" color="danger" on:click={() => deleteIndex()}>🔥Borrar indice</Button>
        </InputGroup>
    </Col>
{/if}
</Col>