<script>
	import { onMount, onDestroy } from "svelte";
	import { Col, Badge, Alert, Image, ListGroup, ListGroupItem, 
		Spinner, InputGroup, Input, InputGroupText, Pagination,
		PaginationItem, PaginationLink } from 'sveltestrap';

	import { indexSelected, flagResourcesLoaded } from '../../stores.js';
	import { LoadGraphics, GetResourceBinary } from '../../indexdb.js';

	let resources = [];
	const maxResourcesPerPage = 100;

	let resourcesList = [];
	let page = 1;
	let maxPages = 1;
	let loaded = false;
	let editingPage = false;
	let pageValue = 1;

	let filterResource = '';
	

	function editPage() {
		if (editingPage==true) {
			if (pageValue>0 && pageValue<=maxPages) {
				changePage(pageValue);
			}
			pageValue = page;
		}
		editingPage = !editingPage;
	}

	function movePage(_move) {
		if (_move == 0 || page + _move < 1 || page + _move > maxPages || loaded == false) {
			return;
		}
		changePage(page + _move);
	}

	function changePage(_page) {
		if (_page <= 0 || _page > maxPages || _page == page || loaded == false) {
			return;
		}
		showPage(_page);
	}

	function showPage(_page) {
		page = _page;
		pageValue = page;

		if (resources.length == 0) {
			return;
		}

		resourcesList = [];
		let count = 0;
		let start = (page - 1) * maxResourcesPerPage;

		resources.forEach(res => {
			// console.log(res);
			count++;
			if (count>start) {
				if (resourcesList.length>=maxResourcesPerPage) {
					return;
				}

				let add = true;
				if (filterResource!=null && String(filterResource).length>0) {
					if (String(res).indexOf(filterResource)==-1) {
						add = false;
					}
				}

				if (add) {
					resourcesList.push(res);
				}
			}
		});
	}

	function selectResource(resource) {
		if ($indexSelected.frames.length == 1 && $indexSelected.speed == 0) {
			$indexSelected.frames[0] = resource;
		}
	}

	async function loadResources() {
		loaded = false;

		resourcesList = [];
		resources = await LoadGraphics();

		if (resources == []) {
			console.log("No resources loaded");
			loaded = true;
			return;
		}

		console.log("Resources loaded: " + resources.length);

		maxPages = Math.ceil(resources.length / maxResourcesPerPage);
		showPage(1);
		loaded = true;
	}

	let unsubscribeResources = flagResourcesLoaded.subscribe(_ => {
		loadResources();
	});

	function findFilter() {
		page = 1;
		loadResources();
	}

	onMount(async () => {
		loadResources();
	});

	onDestroy(async () => {
		unsubscribeResources();
    });

</script>

<Col class="input-dark">
	<InputGroup>
		<Input bind:value={filterResource} on:keyup={findFilter} type="number" placeholder="Número de Imagen" />
	</InputGroup>
	<InputGroup>
		<Pagination style="position: relative; flex: 1 1 auto;">
			<PaginationItem>
				<PaginationLink first href="#" on:click="{() => changePage(1)}" />
			</PaginationItem>
			<PaginationItem>
				<PaginationLink previous href="#" on:click="{() => movePage(-1)}" />
			</PaginationItem>
			<PaginationItem style="position: relative; flex: 1 1 auto;">
				{#if !editingPage }
				<PaginationLink href="#" style="position: relative; flex: 1 1 auto; text-align: center;"
					on:click="{() => editPage()}">{page}</PaginationLink>
				{:else}
					<Input bind:value={pageValue} type="number" placeholder="Página" 
						on:change="{() => editPage()}" on:blur="{() => editPage()}" />
				{/if}
			</PaginationItem>
			<PaginationItem>
				<PaginationLink next href="#" on:click="{() => movePage(1)}" />
			</PaginationItem>
			<PaginationItem>
				<PaginationLink last href="#" on:click="{() => changePage(maxPages)}" />
			</PaginationItem>
		</Pagination>
	</InputGroup>
	{#if (loaded == false)}
		<Spinner primary />
	{:else}
		<ListGroup style="overflow: auto; height: 1050px;">
		{#if resourcesList != undefined && resourcesList != []}
			{#each resourcesList as grhnum}
				<ListGroupItem tag="button" action on:click="{selectResource(grhnum)}" >
					{#await GetResourceBinary(grhnum) then source}
						<Image thumbnail style="min-width:50px;max-width:50px;padding: 0;" src="{source}" alt="Imagen {grhnum}"/>
					{/await}
					{grhnum}
				</ListGroupItem>
			{/each}
		{/if}
		</ListGroup>
	{/if}
</Col>