<script>
  import { flagIndexLoaded } from '../stores.js';
  import { AddGraphic, LoadGraphics, SaveIndexs, LoadIndexs } from '../indexdb.js';

  import { ReadBinaryIndex, ReadStringIndex, ReadBinaryHead, ReadBinaryHelmet, ReadBinaryBodies, ReadBinaryFx } from './logic/indexCompiler.js';

  import {
    Button, Modal, ModalBody, ModalFooter,
    ModalHeader, Row, Col, InputGroup,
    InputGroupText, Input, Spinner, Alert, NavLink,
  } from 'sveltestrap';

  export let mode;

  let open = false;
  let uploading = false;
  let msgError = "";
  let msgResources = "";

  const toggle = () => {
    if (uploading) {
      return;
    }
    msgResources = "";
    msgError = "";
    open = !open;
  }

  function upload() {
    if (uploading) {
      return;
    }

    if (document.getElementById('fileResources').files.length > 0) {

      if (!'indexedDB' in window){
        alert("Tu navegador no soporta IndexedDB");
        return;
      }

      if ('storage' in navigator && 'estimate' in navigator.storage) {
        navigator.storage.estimate().then(function(estimate) {
          console.log('El almacenamiento máximo disponible en indexedDB es de ' + estimate.quota + ' bytes.');
        });
      }
      
      let count = 0;
      let loading = 0;
      msgResources = "Cargando recursos...";
      msgError = "";
      
      for (let i = 0; i < document.getElementById('fileResources').files.length; i++) {
        let file = document.getElementById("fileResources").files[i];

        if (file) {
          let grhNum = file.name.split('.')[0];

          if (file.webkitRelativePath) {
            let pathString = file.webkitRelativePath;
            pathString = pathString.replace(/\\/g, "/")
            pathString = pathString.substring(0,pathString.lastIndexOf("/"+file.name));
            let path = pathString.split('/');
            if (path.length > 1) {
              console.log("GRH ignored by subfolder: " + file.name + " (" + path[0] + ")" + " (" + path[1] + ")");
              continue;
            }
          }
          if (isNaN(grhNum)) {
            console.log("Invalid GRH number: " + file.name);
            continue;
          }

          let body = new Blob;

          var reader = new FileReader();
          reader.onload = function (evt) {
            body = evt.target.result;
            console.log("GRH added: " + file.name);
            // console.log(file);

            const base64body = btoa(body); // base64 encode the file
            AddGraphic(grhNum, base64body);
            
            count++;
            msgResources = count + "/" + loading + " Recursos cargados! 🦎";
            if (count == loading) {
              msgResources = "Inicializando recursos! 🦎";
              msgError = "";
              LoadGraphics();
            }
          };

          reader.onerror = function (evt) {
            msgError = evt;
            return;
          }

          loading++;
          reader.readAsBinaryString(file);
          
        }

      }
      if (msgError != "") {
        return;
      }
    }

    if (document.getElementById('fileIndexs').files.length > 0) {
      if (document.getElementById('fileIndexs').files[0].size > 50000000) {
        msgError = "El archivo seleccionado es muy grande";
        return;
      }
      msgError = "";
      uploading = true;

      function receivedBinary() {
        uploading = false;
        msgResources = "Indice cargado! 🦎";
        toggle();
        const name = document.getElementById('fileIndexs').files[0].name;
        if (name.includes(".ini")) {
          let data = ReadStringIndex(fr.result);
          // $indexsComplete = data;
          SaveIndexs("indexsGraphics", data);

        } else {
          let data = ReadBinaryIndex(fr.result);
          // $indexsComplete = data;
          SaveIndexs("indexsGraphics", data);
        }
        $flagIndexLoaded = true;
        return;
      }

      let fr = new FileReader();
      fr.onload = receivedBinary;
      fr.readAsBinaryString(document.getElementById('fileIndexs').files[0]);
    }

    if (document.getElementById('fileHelmet').files.length > 0) {
      if (document.getElementById('fileHelmet').files[0].size > 5000000) {
        msgError = "El archivo seleccionado es muy grande";
        return;
      }
      msgError = "";
      uploading = true;

      function receivedBinary() {
        uploading = false;
        msgResources = "Cascos cargadas! 🦎";
        toggle();
        let data = ReadBinaryHelmet(fr.result);
        // console.log(data);
        SaveIndexs("indexsHelmet", data); //$indexsHelmet = data;
        return;
      }

      let fr = new FileReader();
      fr.onload = receivedBinary;
      fr.readAsBinaryString(document.getElementById('fileHelmet').files[0]);
    }

    if (document.getElementById('fileBodies').files.length > 0) {
      if (document.getElementById('fileBodies').files[0].size > 5000000) {
        msgError = "El archivo seleccionado es muy grande";
        return;
      }
      msgError = "";
      uploading = true;

      function receivedBinary() {
        uploading = false;
        msgResources = "Cuerpos cargados! 🦎";
        toggle();
        let data = ReadBinaryBodies(fr.result);
        // console.log(data);
        SaveIndexs("indexsBodies", data);// $indexsBodies = data;
        return;
      }

      let fr = new FileReader();
      fr.onload = receivedBinary;
      fr.readAsBinaryString(document.getElementById('fileBodies').files[0]);
    }



    if (document.getElementById('fileHead').files.length > 0) {
      if (document.getElementById('fileHead').files[0].size > 5000000) {
        msgError = "El archivo seleccionado es muy grande";
        return;
      }
      msgError = "";
      uploading = true;

      function receivedBinary() {
        uploading = false;
        msgResources = "Cabezas cargadas! 🦎";
        toggle();
        let data = ReadBinaryHead(fr.result);
        // console.log(data);
        SaveIndexs("indexsHead", data); // $indexsHead = data;
        return;
      }

      let fr = new FileReader();
      fr.onload = receivedBinary;
      fr.readAsBinaryString(document.getElementById('fileHead').files[0]);
    }

    if (document.getElementById('fileFx').files.length > 0) {
      if (document.getElementById('fileFx').files[0].size > 5000000) {
        msgError = "El archivo seleccionado es muy grande";
        return;
      }
      msgError = "";
      uploading = true;

      function receivedBinary() {
        uploading = false;
        msgResources = "FX's cargado! 🦎";
        toggle();
        let data = ReadBinaryFx(fr.result);
        // console.log(data);
        SaveIndexs("indexsFx", data); // $indexsFx = data;
        return;
      }

      let fr = new FileReader();
      fr.onload = receivedBinary;
      fr.readAsBinaryString(document.getElementById('fileFx').files[0]);
    }

    if (msgError.length == 0) {
      // $flagIndexLoaded = true;
    };
  }

</script>
  
<div>
  {#if (mode=='button')}
    <Button color="primary" on:click={toggle} title="Subir un nuevo indice o recursos graficos">Cargar recursos</Button>
  {:else}
    <NavLink color="warning" on:click={toggle} title="Subir un nuevo indice o recursos graficos">Cargar recursos</NavLink>
  {/if}
  <Modal isOpen={open} {toggle} modalClassName="modal-dark">
    <ModalHeader {toggle}>Cargar recursos</ModalHeader>
    <ModalBody>
      <h6>Selecciona la carpeta en donde estan tus graficos BMP o PNG.</h6>
      <Row>
        <Col>
          <InputGroup class="input-dark">
            <Input type="file" name="fileResources" id="fileResources" directory webkitdirectory mozdirectory multiple />
            <!-- webkitdirectory directory  -->
            <InputGroupText>
              Recursos
            </InputGroupText>
          </InputGroup>
        </Col>
      </Row>
      <br/>
      <h6>Selecciona los indices con los que vas a trabajar.</h6>
      <Row>
        <Col>
          <InputGroup class="input-dark">
            
            <Input type="file" name="fileIndexs" id="fileIndexs" />
            <InputGroupText>
              Indices de Graficos (🟢 ini/ind)
            </InputGroupText>
          </InputGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroup class="input-dark">
            <Input type="file" name="fileHead" id="fileHead" />
            <InputGroupText>
              Indices de Cabezas (🟢 ind)
            </InputGroupText>
          </InputGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroup class="input-dark">
            <Input type="file" name="fileHelmet" id="fileHelmet" />
            <InputGroupText>
              Indices de Cascos (🟢 ind)
            </InputGroupText>
          </InputGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroup class="input-dark">
            <Input type="file" name="fileBodies" id="fileBodies" />
            <InputGroupText>
              Indices de Cuerpos (🟢 dat)
            </InputGroupText>
          </InputGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroup class="input-dark">
            <Input type="file" name="fileFx" id="fileFx" />
            <InputGroupText>
              Indices de FX's (🟢 ind)
            </InputGroupText>
          </InputGroup>
        </Col>
      </Row>      
    </ModalBody>
    <ModalFooter>
      {#if (msgResources)}
        <Row><Alert color="success">{msgResources}</Alert></Row>
      {/if}
      {#if (msgError)}
        <Row><Alert color="danger">{msgError}</Alert></Row>
      {/if}
      {#if !uploading} 
        <Row>
          <Col><Button color="primary" on:click={upload}>Cargar</Button></Col>
          <Col><Button color="secondary" on:click={toggle}>Cancelar</Button></Col>
        </Row>
      {:else}
        <Spinner primary  />
      {/if}
    </ModalFooter>
  </Modal>
</div>