function stringToUTF8Bytes(string) {
	let utf8 = new Uint8Array(string.length);
	for (let i = 0; i < string.length; i++) {
		let charcode = string.charCodeAt(i);
		utf8[i] = charcode;
	}
	return utf8;
}

function UTF8BytesToString(bytes) {
	let string = "";
	for (let i = 0; i < bytes.length; i++) {
		string += String.fromCharCode(bytes[i]);
	}
	return string;
}

function readHeader(arrayBuffer) {
	let header = arrayBuffer.slice(0, 255);

	for (let i = 0; i < header.length; i++) {
		if (header[i] < 32 || header[i] > 126) {
			return 0;
		}
	}

	let offset = 255;
	let crc;
	let magic;


	[crc, offset] = readLong(arrayBuffer, offset);
	if (crc > 100) {
		return offset;
	}

	[magic, offset] = readLong(arrayBuffer, offset);
	if (magic > 10) {
		return offset;
	}

	return offset
}

export function ReadBinaryFx(binaryFile) {
	let arrayBuffer = stringToUTF8Bytes(binaryFile);

	// console.log("completo ",arrayBuffer);

	let offset = readHeader(arrayBuffer);
	// console.log("offset ",offset);

	let Fxs = [];
	let totalFx;

	[totalFx, offset] = readInteger(arrayBuffer, offset);

	for (let i = 0; i < totalFx; i++) {
		let tempFx = {
			index: 0,
			anim: 0,
			offsetX: 0,
			offsetY: 0,
			isPNG: 0,
		};

		tempFx.index = i + 1;
		[tempFx.anim, offset] = readLong(arrayBuffer, offset);
		[tempFx.offsetX, offset] = readInteger(arrayBuffer, offset);
		[tempFx.offsetY, offset] = readInteger(arrayBuffer, offset);

		let value;
		let tempOffset;
		[value, tempOffset] = readInteger(arrayBuffer, offset);
		if (value == 0 || value == 1)  {
			if (value == 1) {
				tempFx.isPNG = 1;
			} else {
				tempFx.isPNG = 0;
			}
			offset = tempOffset;
		}

		if (tempFx.anim > 0) {
			Fxs.push(tempFx);
		}
	}

	return Fxs;
}

export function ReadBinaryHead(binaryFile) {
	let arrayBuffer = stringToUTF8Bytes(binaryFile);

	// console.log("completo ",arrayBuffer);

	let offset = readHeader(arrayBuffer);
	// console.log("offset ",offset);

	let Heads = [];
	let totalHeads;

	[totalHeads, offset] = readInteger(arrayBuffer, offset);

	for (let i = 0; i < totalHeads; i++) {
		let tempHead = {
			index: 0,
			head: [],
		};

		tempHead.index = i + 1;
		[tempHead.head[0], offset] = readLong(arrayBuffer, offset);
		[tempHead.head[1], offset] = readLong(arrayBuffer, offset);
		[tempHead.head[2], offset] = readLong(arrayBuffer, offset);
		[tempHead.head[3], offset] = readLong(arrayBuffer, offset);

		Heads.push(tempHead);
	}

	return Heads;
}

