export function ReadStringIndex(indexFile) { // graficos.ini
	let arrayBuffer = stringToUTF8Bytes(indexFile);

	let index = {
		version: 0,
		maxGrh: 0,
		indexs: [],
	};

	let offset = 0;
	let temp = "";

	let tempInit = false;

	while (offset < arrayBuffer.length) {
		temp = "";
		while (arrayBuffer[offset] != 10 && offset < arrayBuffer.length) {
			temp += String.fromCharCode(arrayBuffer[offset]);
			offset++;
		}
		offset++;

		if ("" == temp.trim()) {
			continue;
		}

		if ("[INIT]" == temp.trim().toUpperCase()) {
			tempInit = true;
			continue;
		}

		if ("[GRAPHICS]" == temp.trim().toUpperCase()) {
			tempInit = false;
			continue;
		}

		if (tempInit) {
			let data = temp.split("=");
			if (data[0] == "NumGrh") {
				index.maxGrh = parseInt(data[1]);
			} else if (data[0] == "Version") {
				index.version = parseFloat(data[1]);
			}
		}

		if (!tempInit) {
			let data = temp.split("=");
			let tempIndex = {
				index: parseInt(data[0].substring(3)),
				frames: [],
				x: 0,
				y: 0,
				w: 0,
				h: 0,
				speed: 0.0,
			};

			let tempData = data[1].split("-");
			if (tempData[0] == "1") {
				tempIndex.frames.push(parseInt(tempData[1]));
				tempIndex.x = parseInt(tempData[2]);
				tempIndex.y = parseInt(tempData[3]);
				tempIndex.w = parseInt(tempData[4]);
				tempIndex.h = parseInt(tempData[5]);
			} else {
				for (let i = 1; i < tempData.length - 1; i++) {
					tempIndex.frames.push(parseInt(tempData[i]));
				}
				tempIndex.speed = parseFloat(tempData[tempData.length - 1]);
			}

			if (tempIndex.index > index.maxGrh) {
				console.log("Grh index ", tempIndex.index, " is greater than max grh");
				return index;
			}

			if (tempIndex.index > 0) {
				index.indexs.push(tempIndex);
			}
		}
	}

	console.log("Index version:", index.version);
	console.log("Max grh:", index.maxGrh);
	console.log("Index length:", index.indexs.length);

	return index;
}

export function ReadBinaryIndexInteger(binaryFile) { // graficos.ind
	let arrayBuffer = stringToUTF8Bytes(binaryFile);

	let index = {
		version: 0,
		maxGrh: 0,
		indexs: [],
	};

	let offset = 0;
	let temp = "";

	// read header if exists
	offset = readHeader(arrayBuffer);

	console.log("offset ", offset);

	offset += (5*2); // 5 integers

	console.log("first offset", offset);

	index.version = 1;
	index.maxGrh = 32767;

	for (let i = 0; i < index.maxGrh; i++) {
		let tempIndex = {
			index: 0,
			frames: [],
			x: 0,
			y: 0,
			w: 0,
			h: 0,
			speed: 0.0,
		};

		[tempIndex.index, offset] = readInteger(arrayBuffer, offset);
		if (tempIndex.index <= 0) {
			if (index.indexs.length == 0) {
				console.log("Grh index ", tempIndex.index, " is less than 0");
				return index;
			}
			break;
		}

		let frames = 0;
		[frames, offset] = readInteger(arrayBuffer, offset);

		if (frames == 1) {
			tempIndex.frames.push(readInteger(arrayBuffer, offset)[0]);
			offset += 2;
			[tempIndex.x, offset] = readInteger(arrayBuffer, offset);
			[tempIndex.y, offset] = readInteger(arrayBuffer, offset);
			[tempIndex.w, offset] = readInteger(arrayBuffer, offset);
			[tempIndex.h, offset] = readInteger(arrayBuffer, offset);
		} else if (frames > 1) {
			for (let j = 0; j < frames; j++) {
				tempIndex.frames.push(readInteger(arrayBuffer, offset)[0]);
				offset += 2;
			}
			[tempIndex.speed, offset] = readInteger(arrayBuffer, offset);
		}

		if (tempIndex.index > index.maxGrh) {
			console.log("Grh index ", tempIndex.index, " is greater than max grh");
			return index;
		}

		if (tempIndex.index >0) {
			console.log(tempIndex);
			index.indexs.push(tempIndex);
		}
	}

	console.log("Index length:", index.indexs.length);

	return index;
}

