export function CompileBinaryIndex(index) {
    var binaryFile = [];

	binaryFile.push(writeLong(index.version));
	binaryFile.push(writeLong(index.maxGrh));

	for (var i = 0; i < index.indexs.length; i++) {
		binaryFile.push(writeLong(index.indexs[i].index));
		binaryFile.push(writeInteger(index.indexs[i].frames.length));

		if (index.indexs[i].frames.length == 1) {
			binaryFile.push(writeLong(index.indexs[i].frames[0]));
			binaryFile.push(writeInteger(index.indexs[i].x));
			binaryFile.push(writeInteger(index.indexs[i].y));
			binaryFile.push(writeInteger(index.indexs[i].w));
			binaryFile.push(writeInteger(index.indexs[i].h));
		} else if (index.indexs[i].frames.length > 1) {
			for (var j = 0; j < index.indexs[i].frames.length; j++) {
				binaryFile.push(writeLong(index.indexs[i].frames[j]));
			}
			binaryFile.push(writeDouble(index.indexs[i].speed));
		}
	}

	return binaryFile;
}

export function CompileIniIndex(index) {
	var iniFile = "";

	iniFile += "[INIT]\n";
	iniFile += "NumGrh=" + index.maxGrh + "\n";
	iniFile += "Version=" + index.version + "\n\n";

	iniFile += "[Graphics]\n";
	for (var i = 0; i < index.indexs.length; i++) {
		iniFile += "Grh" + index.indexs[i].index + "=";
		iniFile += index.indexs[i].frames.length + "-";

		if (index.indexs[i].frames.length == 1) {
			iniFile += index.indexs[i].frames[0] + "-";
			iniFile += index.indexs[i].x + "-";
			iniFile += index.indexs[i].y + "-";
			iniFile += index.indexs[i].w + "-";
			iniFile += index.indexs[i].h + "\n";
		} else if (index.indexs[i].frames.length > 1) {
			for (var j = 0; j < index.indexs[i].frames.length; j++) {
				iniFile += index.indexs[i].frames[j] + "-";
			}
			iniFile += index.indexs[i].speed + "\n";
		}
	}

	return iniFile;
}