import { writable } from 'svelte/store';

Storage.prototype.setObject = function(key, value) {
    this.setItem(key, JSON.stringify(value));
}

Storage.prototype.getObject = function(key) {
    var value = this.getItem(key);
    if (value == "null" || value == "undefined" || value == "") {
        return 0 && JSON.parse(0);
    }
    return value && JSON.parse(value);
}

export const indexsGraphics = writable(0);
export const resourcesGraphics = writable(0);

export const sectionActive = writable(window.localStorage.getObject("sectionActive")) || "";
export const indexSelected = writable(0);
export const fxSelected = writable(0);
export const headSelected = writable(0);
export const helmetSelected = writable(0);
export const bodiesSelected = writable(0);

export const flagIndexLoaded = writable(window.localStorage.getObject("flagIndexLoaded"));
export const flagResourcesLoaded = writable(window.localStorage.getObject("flagResourcesLoaded"));

export const events = writable(0)

flagIndexLoaded.subscribe(value => {
    if (value == null) {
        value = false
    }
    window.localStorage.setObject("flagIndexLoaded", value);
});

flagResourcesLoaded.subscribe(value => {
    if (value == null) {
        value = false
    }
    window.localStorage.setObject("flagResourcesLoaded", value);
});
