<script>
    import { onMount, onDestroy, afterUpdate  } from "svelte";
    import { flagIndexLoaded } from '../../stores.js';
	import { GetResourceBinary, LoadIndexs } from '../../indexdb.js';

	export let grhNum;

	let indexs = [];
	let indexData = undefined;
	let animation = undefined;
	let runningAnimation = false;
	let speedAnimation = 0;
	const minSpeedAnimation = 100;

	let imageLoaded = 0;
	let imgSource = document.createElement('img');
	let imgTransp = document.createElement('img');

	function RemoveBackground(img) {

		if (img==undefined) {
			return;
		}

		let buffer = document.createElement('canvas');
		let bufferctx = buffer.getContext('2d');

		buffer.width = img.width;
		buffer.height = img.height;
		bufferctx.drawImage(img, 0, 0);
		bufferctx.drawImage(img, 0, 0);

		let imageData = bufferctx.getImageData(0, 0, buffer.width,  buffer.height);	
		let data = imageData.data;
		let transparent = [data[data.length-4],data[data.length-3],data[data.length-2]];
		let similar = 9;

		for (let i = 0; i < data.length; i += 4) {
			if((data[i]>=transparent[0]-similar && data[i]<=transparent[0]+similar) && // R
			(data[i + 1]>=transparent[1]-similar && data[i + 1]<=transparent[1]+similar) && // G
			(data[i + 2]>=transparent[2]-similar && data[i + 2]<=transparent[2]+similar)) { // B
				data[i + 3] = 0; // alpha
			}
		}

		bufferctx.putImageData(imageData, 0, 0);

		let ret = new Image();
		ret = buffer.toDataURL();
		return ret;
	}

	async function showGrh(index) {

		if (index == undefined) {
			return;
		}

		if (index.speed > 0 && index.frames.length>1) {
			return;
		}

		let canvas = document.getElementById("main");
		let ctx = canvas.getContext('2d');

		ctx.imageSmoothingEnabled = false;
		ctx.imageSmoothingQuality = 'high';
		ctx.fillStyle = "green";
		ctx.fillRect(0, 0, canvas.width, canvas.height);

		if (imageLoaded == index.frames[0]) {
			console.log("Cargado de cache " + imageLoaded);

			var ratio = Math.min(canvas.width / index.w, canvas.height / index.h);
			ctx.drawImage(imgTransp, index.x, index.y,
				index.w, index.h,
				0, 0, index.w*ratio, index.h*ratio);

			return;
		} else {
			imageLoaded = 0;
		}

		imgSource.crossOrigin = "Anonymous";
		imgSource.onload = function() {
			imgTransp.onload = function () {
				var ratio = Math.min(canvas.width / index.w, canvas.height / index.h);
				ctx.drawImage(imgTransp, index.x, index.y,
					index.w, index.h,
					0, 0, index.w*ratio, index.h*ratio);
			}
			imgTransp.src = RemoveBackground(imgSource);
		}

		console.log("Cargando " , index.frames[0]);

		GetResourceBinary(index.frames[0]).then(function(img) {	
			imgSource.src = img;
			imageLoaded = index.frames[0];
		});
	}

	function showAnimation(index) {

		if (index == undefined) {
			return;
		}

		if (runningAnimation) {
			clearInterval(animation);
			runningAnimation = false;
		}

		let frame = 0;

		if (index.speed > minSpeedAnimation*2) {
			speedAnimation = minSpeedAnimation;
		} else if (speedAnimation < minSpeedAnimation/2) {
			speedAnimation = index.speed*100;
		}

		if (speedAnimation < minSpeedAnimation) {
			speedAnimation = minSpeedAnimation;
		};

		animation = setInterval(()=> {
			if ((!runningAnimation) ||
				(speedAnimation < minSpeedAnimation) ||
				(indexs == undefined) || 
				(index.index == undefined)) {

				runningAnimation = false;
				return;
			}

			if (frame == index.frames.length) {
				frame = 0;
			}

			indexs.indexs.forEach(i => {
				if (i.index == index.frames[frame]) {
					showGrh(i);
				}
			});
			frame++;

		}, speedAnimation);

		runningAnimation = true;
	}

	let unsubscribeGraphics = flagIndexLoaded.subscribe(_ => {
		loadGrh()
	});

	async function loadGrh() {
		if (flagIndexLoaded) {
			indexs = await LoadIndexs("indexsGraphics");
		} else {
			indexs = [];
			return;
		}

		if (grhNum == undefined) {
			return;
		}

		indexData = [];
	
		indexs.indexs.forEach(i => {
			if (i.index == grhNum) {
				indexData = i;
			}
		});

		if (indexData == undefined) {
			return;
		}
		
		if (runningAnimation) {
			runningAnimation = false;
			clearInterval(animation);
		}

		if (indexData.speed > 0 && indexData.frames.length>1) {
			showAnimation(indexData);
		} else {
			if (runningAnimation) {
				clearInterval(animation);
				runningAnimation = false;
			}
			showGrh(indexData);
		}
	}

    onMount(async () => {
		loadGrh(grhNum);
	});

	afterUpdate(() => {
		loadGrh(grhNum);
	});

    onDestroy(async () => {
		imageLoaded = 0;
        imgSource = document.createElement('img');
		imgTransp = document.createElement('img');
        if (runningAnimation) {
			runningAnimation = false;
			clearInterval(animation);
		}
		unsubscribeGraphics();
    });

</script>

<canvas id="main" width="300px" height="300px"></canvas>

<style>
	canvas {
        border:1px solid #000000;
        background-color: black;
		border-color: black;
        display: block;
        margin: 0 auto;
	}
</style>