<script>
	import { onDestroy, onMount } from "svelte";
	import { Row, Col, Badge, Alert, Image, ListGroup, ListGroupItem,
		Spinner, InputGroup, Input, InputGroupText, Nav, Navbar, NavbarBrand,
		NavItem, NavLink, Button } from 'sveltestrap';

	import { resourcesGraphics, indexsGraphics, flagIndexLoaded, flagResourcesLoaded,
			indexSelected, sectionActive } from '../stores.js';
	import { LoadGraphics, LoadIndexs } from '../indexdb.js';

	import Landing from "./landing.svelte";

	import ModalLoadResources from '../components/modalLoadResources.svelte';
	import IndexListRight from '../components/studio/indexListRight.svelte';
	import IndexListLeft from '../components/studio/indexListLeft.svelte';
	import IndexEdit from '../components/studio/indexEdit.svelte';

	import IndexHeadListRight from '../components/studio/indexHeadListRight.svelte';
	import IndexHeadEdit from '../components/studio/indexHeadEdit.svelte';

	import IndexHelmetListRight from '../components/studio/indexHelmetListRight.svelte';
	import IndexHelmetEdit from '../components/studio/indexHelmetEdit.svelte';

	import IndexBodiesListRight from '../components/studio/indexBodiesListRight.svelte';
	import IndexBodiesEdit from '../components/studio/indexBodiesEdit.svelte';

	import IndexFxListRight from '../components/studio/indexFxListRight.svelte';
	import IndexFxEdit from '../components/studio/indexFxEdit.svelte';

	let indexs = [];
	let heads = [];
	let helmets = [];
	let bodies = [];
	let fxs = [];

	let messageError = "";
	let showDebug = false;

	let loadResources = async () => {
		if ($flagResourcesLoaded) {
			LoadGraphics();
		}
	}

	let loadIndex = async () => {
		if ($flagIndexLoaded) {
			indexs = await LoadIndexs("indexsGraphics");
			heads = await LoadIndexs("indexsHead");
			helmets = await LoadIndexs("indexsHelmet");
			bodies = await LoadIndexs("indexsBodies");
			fxs = await LoadIndexs("indexsFx");
		} else {
			indexs = [];
		}
		console.log(indexs);
	}

	let unsubscribeResources = flagResourcesLoaded.subscribe(_ => {
		loadResources();
	});

	let unsubscribeGraphics = flagIndexLoaded.subscribe(_ => {
		loadIndex();
	});

	onMount(async () => {
		messageError = "";
		if($sectionActive == null) {
			$sectionActive = "";
		}
		indexSelected.set(null);
		loadResources();
		loadIndex();
	});

	onDestroy(async () => {
		unsubscribeResources();
		unsubscribeGraphics();
	});

</script>

{#if (messageError != "")}
	<Row>
		<Col class="input-dark">
			<Alert color="danger">
				{messageError}
			</Alert>
		</Col>
	</Row>
{/if}

{#if (!$flagResourcesLoaded && !$flagIndexLoaded)}
	<Row>
		<Col class="text-center" style="padding: 2em; color: white;">
			<div style="padding-bottom: 1em;">
				Para comenzar, carga los recursos necesarios.
				<ModalLoadResources mode="button" />
			</div>
		</Col>
	</Row>

	<Landing />
{/if}

{#if ($flagResourcesLoaded || $flagIndexLoaded)}
	<Row cols={{ md: 3, sm: 1 }}>
		<IndexListLeft/>
		{#if ($sectionActive == '')}
			<div class="relative">
				<IndexEdit />
			</div>
			<IndexListRight />
		{:else if $sectionActive == 'head'}
			<div class="relative">
				<IndexHeadEdit />
			</div>
			<IndexHeadListRight />
		{:else if $sectionActive == 'helmet'}
			<div class="relative">
				<IndexHelmetEdit />
			</div>
			<IndexHelmetListRight />
		{:else if $sectionActive == 'body'}
			<div class="relative">
				<IndexBodiesEdit />
			</div>
			<IndexBodiesListRight />
		{:else if $sectionActive == 'fx'}
			<div class="relative">
				<IndexFxEdit />
			</div>
			<IndexFxListRight />
		{/if}
	</Row>
{/if}

<Row class="debugPanel">
	<Col>
		<span on:click={() => showDebug = !showDebug} on:keydown={() => showDebug = !showDebug} class="cursor">Click to show Debug</span>
		{#if showDebug}<br/><br/>
		ResourcesLoaded: {$flagResourcesLoaded} (total: {($resourcesGraphics != undefined && $resourcesGraphics != null) ? $resourcesGraphics.length : 0})<br/>
		IndexsLoaded: {$flagIndexLoaded == true}  (total: {(indexs == undefined || indexs == [] || indexs.indexs != undefined) ? indexs.indexs.length : 0})<br/>
		IndexsHead: {(heads != undefined && heads != null) ? heads.length : 0}<br/>
		IndexsHelmet: {(helmets != undefined && helmets != null) ? helmets.length : 0}<br/>
		IndexsBodies: {(bodies != undefined && bodies != null) ? bodies.length : 0}<br/>
		IndexsFx: {(fxs != undefined && fxs != null) ? fxs.length : 0}<br/>
		{/if}
	</Col>
</Row>

<style>
	.relative {
		position: relative;
	}
</style>