<script>
  import { indexSelected, flagIndexLoaded,  flagResourcesLoaded } from '../stores.js';
  import { CleanGraphics, CleanIndexs } from '../indexdb.js';

  import ModalLoadResources from './modalLoadResources.svelte';

  import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    Button,
    NavLink,
    Dropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem
  } from 'sveltestrap';

  let isOpen = false;

  function handleUpdate(event) {
    isOpen = event.detail.isOpen;
  }

  function newIndexs() {
    indexSelected.set(undefined);
    indexsComplete.set({"max_grh": 1, "version": 1, "indexs": [
      {"index":1,frames:[0], x: 0, y: 0, w: 0, h: 0, speed: 0},
    ]});
    flagIndexLoaded.set(true);
  }

  function cleanAll() {
    flagIndexLoaded.set(false);
    flagResourcesLoaded.set(false);
    // indexSelected.set(undefined);
    // indexsComplete.set(undefined);
    // indexsHead.set(undefined);
    // indexsHelmet.set(undefined);
    // indexsBodies.set(undefined);
    // indexsFx.set(undefined);
    CleanGraphics();
    CleanIndexs();
  }

</script>
  

  <Navbar color="dark" dark expand="md" pills>
    <NavbarBrand href="#" style="color: gold;">IndeXAO</NavbarBrand>
    <NavbarToggler on:click={() => (isOpen = !isOpen)} />
    <Collapse {isOpen} navbar expand="md" on:update={handleUpdate}>
      <Nav class="ms-auto" navbar>
        {#if ($flagResourcesLoaded && $flagIndexLoaded)}
        <NavItem>
          <NavLink style="" title="Ver listado de indices">Indices</NavLink>
        </NavItem>
        {/if}
        <NavItem>
            <ModalLoadResources />
        </NavItem>
        {#if ($flagResourcesLoaded || $flagIndexLoaded)}
        <NavItem>
          <NavLink on:click={() => newIndexs()} color="success" title="Crear nuevo indice">Nuevo</NavLink>
        </NavItem>
        {/if}
        {#if ($flagResourcesLoaded || $flagIndexLoaded)}
        <NavItem>
          <NavLink on:click={() => cleanAll()} color="danger" title="Borrar todos los recursos e indices cargados" alt="Limpiar y vaciar el cache del sitio">Borrar</NavLink>
        </NavItem>
        {/if}
        <!-- <Dropdown nav inNavbar>
          <DropdownToggle nav caret>Options</DropdownToggle>
          <DropdownMenu end>
            <DropdownItem>Option 1</DropdownItem>
            <DropdownItem>Option 2</DropdownItem>
            <DropdownItem divider />
            <DropdownItem>Reset</DropdownItem>
          </DropdownMenu>
        </Dropdown> -->
      </Nav>
    </Collapse>
  </Navbar>


