<script>
    import { Row, Col, Badge, Alert, Image, ListGroup, ListGroupItem, 
		Spinner, Button, InputGroup, Input, InputGroupText } from 'sveltestrap';

    import { fxSelected, events } from '../../stores.js';

    import IndexPreviewGrh from './indexPreviewGrh.svelte';

</script>

