import { resourcesGraphics, flagResourcesLoaded, flagIndexLoaded } from "./stores.js";

var indexedDB = window.indexedDB || window.mozIndexedDB || window.webkitIndexedDB || window.msIndexedDB || window.shimIndexedDB;

const DBResources = "DBResources";

const GraphicsStore = "GraphicsStore";
const GraphicsIndex = "GraphicIndex";

const IndexsStore = "IndexsStore";
const IndexsIndex = "IndexsIndex";

const IndexTags = ["indexsGraphics", "indexsFx", "indexsHead", "indexsHelmet", "indexsBodies"];

let cache = [];

const version = 1;
var db = null;

function initializeDB() {
    return new Promise((resolve, reject) => {
        const request = indexedDB.open(DBResources, version);

        request.onupgradeneeded = function(e) {
            db = e.target.result;
            if (!db.objectStoreNames.contains(GraphicsStore)) {
                var store = db.createObjectStore(GraphicsStore);
                store.createIndex(GraphicsIndex, ["id"]);
            }

            if (!db.objectStoreNames.contains(IndexsStore)) {
                var store = db.createObjectStore(IndexsStore);
                store.createIndex(IndexsIndex, ["id"]);
            }
        };

        request.onsuccess = function(e) {
            db = e.target.result;
            console.log("IndexedDB initialized");
            resolve();
        };

        request.onerror = function(e) {
            reject("Error initializing database");
        };
    });
}

export async function SaveIndexs(tag, data) {
    if (!db) {
        await initializeDB();
    }

    console.log("IndexedDB.SaveIndexs: " + tag);

    if (IndexTags.indexOf(tag) == -1) {
        console.log("IndexedDB.SaveIndexs: Error: Invalid tag: " + tag);
        return;
    }

    return new Promise((resolve, reject) => {
        var tx = db.transaction(IndexsStore, "readwrite");
        var store = tx.objectStore(IndexsStore);
        var putRequest = store.put({id: tag, data: data}, tag);

        putRequest.onsuccess = function() {
            cache[tag] = data; // update cache
            resolve();
        };

        putRequest.onerror = function(e) {
            console.error("IndexedDB.SaveIndexs: Error:", e);
            reject("Error saving indexs");
        };
    });
}

export async function LoadIndexs(tag) {
    if (!db) {
        await initializeDB();
    }

    console.log("IndexedDB.LoadIndexs: " + tag);

    if (IndexTags.indexOf(tag) == -1) {
        console.log("IndexedDB.LoadIndexs: Error: Invalid tag: " + tag);
        return;
    }

    if (cache[tag] !== undefined) {
        console.log("IndexedDB.LoadIndexs: Cache hit for tag " + tag);
        return cache[tag];
    }

    let returned = undefined;
    try {
        returned = new Promise((resolve) => {
            var tx = db.transaction(IndexsStore, "readwrite");
            var store = tx.objectStore(IndexsStore);
            var getAll = store.get(tag);

            getAll.onsuccess = function() {
                if (getAll.result == undefined) {
                    console.log("IndexedDB.LoadIndexs: No indexs found [" + tag + "]");
                    resolve([]);
                    return;
                }
                if (flagIndexLoaded) {
                    flagIndexLoaded.set(true);
                }
                cache[tag] = getAll.result.data;
                resolve(getAll.result.data);
            }

            getAll.onerror = function(e) {
                console.error("IndexedDB.LoadIndexs: Error:", e);
                resolve([]);
            };
        });
    } catch (e) {
        console.log("IndexedDB.LoadIndexs: Error: " + e);
    }

    return await returned;
}

export async function CleanIndexs() {
    if (!db) {
        await initializeDB();
    }

    console.log("IndexedDB.CleanIndexs Init");

    cache = [];

    let returned = undefined;
    try {
        returned = new Promise((resolve) => {
            var tx = db.transaction(IndexsStore, "readwrite");
            var store = tx.objectStore(IndexsStore);
            store.clear();

            resolve(true);
        });
    } catch (e) {
        console.log("IndexedDB.CleanIndexs: Error: " + e);
    }

    return await returned;
}

// RESOURCES //

export async function AddGraphic(grhnum, body) {
    if (!db) {
        await initializeDB();
    }

    // console.log("IndexedDB.AddGraphic: " + grhnum);

    return new Promise((resolve, reject) => {
        var tx = db.transaction(GraphicsStore, "readwrite");
        var store = tx.objectStore(GraphicsStore);
        var putRequest = store.put({id: parseInt(grhnum), body: body}, parseInt(grhnum));

        putRequest.onsuccess = function() {
            resolve();
        };

        putRequest.onerror = function(e) {
            console.error("IndexedDB.AddGraphic: Error:", e);
            reject("Error adding graphic");
        };
    });
}

export async function CleanGraphics() {
    if (!db) {
        await initializeDB();
    }

    console.log("IndexedDB.CleanGraphics Init");

    let returned = undefined;
    try {
        returned = new Promise((resolve) => {
            var tx = db.transaction(GraphicsStore, "readwrite");
            var store = tx.objectStore(GraphicsStore);
            store.clear();

            resourcesGraphics.set([]);
            flagResourcesLoaded.set(false);

            tx.oncomplete = function() {
                // db.close();
                resolve(true);
            };
        });
    } catch (e) {
        console.log("IndexedDB.CleanGraphics: Error: " + e);
    }

    return await returned;
}

export async function LoadGraphics() {
    if (!db) {
        await initializeDB();
    }

    console.log("IndexedDB.LoadGraphics Init");

    let returned = undefined;
    try {
        returned = new Promise((resolve) => {
            var tx = db.transaction(GraphicsStore, "readwrite");
            var store = tx.objectStore(GraphicsStore);
            var getAll = store.getAll();

            getAll.onsuccess = function() {
                if (getAll.result.length == 0) {
                    console.log("IndexedDB.LoadGraphics: No resources found");
                    resourcesGraphics.set([]);
                    flagResourcesLoaded.set(false);
                    resolve([]);
                    return;
                } else {
                    console.log("IndexedDB.LoadGraphics: Resources found: " + getAll.result.length);
                }

                let list = [];
                for (var i = 0; i < getAll.result.length; i++) {
                    if (getAll.result[i].id>0) {
                        list.push(getAll.result[i].id);
                    }
                }

                resourcesGraphics.set(list);
                flagResourcesLoaded.set(true);

                console.log("IndexedDB.LoadGraphics: Resources loaded: " + list.length);

                resolve(list);
            }
        });
    } catch (e) {
        console.log("IndexedDB.LoadGraphics: Error: " + e);
    }

    return await returned;
}

export async function GetResourceBinary(grhnum) {
    if (!db) {
        await initializeDB();
    }

    // console.log("IndexedDB.GetResourceBinary Init: " + grhnum);

    let returned = undefined;
    try {
        returned = new Promise((resolve) => {
            var tx = db.transaction(GraphicsStore, "readwrite");
            var store = tx.objectStore(GraphicsStore);
            var getAll = store.get(parseInt(grhnum));

            getAll.onsuccess = function() {
                if (getAll.result == undefined) {
                    console.log("IndexedDB.GetResourceBinary: No resource found");
                    resolve("");
                    return;
                }
                resolve("data:image/png;base64," + (getAll.result.body.toString('base64'))); // btoa
            }
        });
    } catch (e) {
        console.log("IndexedDB.GetResourceBinary: Error: " + e);
    }

    return await returned;

}